.containerForImages {
  display: flex;
  column-gap: 20px;
  box-sizing: border-box;
  justify-content: flex-start;
}
.containerForImages .blockForImage {
  background-color: #cbcbcb;
  box-sizing: border-box;
  width: 335px;
  height: 315px;
  position: relative;
  box-shadow: 0px 0px 15px 2px #838383;
}
.containerForImages .blockForImage img {
  top: -15px;
  width: 320px;
  position: absolute;
}

